export class Analytics {
    constructor() {
    }

    static init(appId:string):void {
        window["_gameGATracker"]("create", appId, "auto");
    };

    static logPageview(params):void {
        window["_gameGATracker"]("send", "pageview", params);
    };

    static logEvent(category, action, label, value):void {
        window["_gameGATracker"]("send", "event", category, action, label, value);
    };

    static customDimension(dimension, value):void {
        window["_gameGATracker"]("set", "dimension" + dimension, value);
    }
};


