import {GameSettings} from "../settings/GameSettings";



export class Background
{
    private _container: PIXI.Container;
    private _tilingBackground: PIXI.extras.TilingSprite;


    public constructor(container: PIXI.Container)
    {
        this._container = container;
        this._tilingBackground = new PIXI.extras.TilingSprite(PIXI.Texture.fromImage("background_tile"), GameSettings.width, GameSettings.height);
        this._container.addChild( this._tilingBackground );
    }


    public destroy():void
    {
        this._container = null;
    }


    public resize(playAreaRect: PIXI.Rectangle):void
    {
        this._tilingBackground.width = GameSettings.width;
        this._tilingBackground.height = GameSettings.height;
    }

}


