export class MissionEvents
{
    public static activeMissions: Array<MissionData>;
    private static _emitter: PIXI.utils.EventEmitter;



    public static setActiveMissions():void
    {
        MissionEvents.activeMissions = new Array<MissionData>();
        for(let i:number = 0; i < GameSettings.openedTrophies.length; i++)
        {
            let m: MissionData = MissionDescriptions.data[GameSettings.openedTrophies[i]];
            MissionEvents.activeMissions.push(m);
        }
    }


    public static removeEvents():void
    {
        MissionEvents._emitter.removeAllListeners();
    }
    

    // game events for each mission type are checked against the active mission list
    // the progress is updated for matches
    public static addMissionEvents(_emitter: PIXI.utils.EventEmitter):void
    {
        //
        // local functions
        //

        // advance all active missions one step towards completion
        function missionCheatTest(completed: Function):void
        {
            for(let i:number = 0, l:number = MissionEvents.activeMissions.length; i < l; i++)
            {
                if (MissionEvents.activeMissions[i])
                {
                    let type: string = MissionEvents.activeMissions[i]._type;
                    missionCompleted(type, 1, completed);
                }
            }
        }

        function allMissionsCheatTest(completed: Function):void
        {
            for(let i:number = 0, l:number = MissionEvents.activeMissions.length; i < l; i++)
            {
                if (MissionEvents.activeMissions[i])
                {
                    let type: string = MissionEvents.activeMissions[i]._type;
                    let active:Array<MissionData> = MissionEvents.activeMissionType(type);
                    active[0]._completed = active[0]._required;
                    missionCompleted(type, active[0]._goalNum, completed);
                }
            }
        }

        // check if an active mission 'type' exists and whether it achieved its 'completed' requirements
        function missionCompleted(type: string, change: number, completed: Function)
        {
            // are there 'type' missions active
            let active:Array<MissionData> = MissionEvents.activeMissionType(type);
            for(let i:number = 0, l:number = active.length; i < l; i++)
            {
                let data:MissionData = active[i];

                // update our progress towards the next goal
                data._goalProgress += change;

                // did we complete any active mission goals
                if (completed(data))
                {
                    data._completed++;

                    // did we just finish an entire mission?
                    if (data._completed >= data._required)
                    {
                        if (GameSettings.consoleLogging)
                        {
                            console.log("*** mission '" + type + "'' completed! ***" + " " + data._description);
                        }

                        Analytics.logEvent("Badges", data._name, "", "");

                        // signal that this trophy was completed during this game
                        data._newlyCompleted = true;
                        //_emitter.emit("mission_complete", data);    // not needed at present, there is no notification of this
                        // find the mission in the activeMissions list and change it to the next mission
                        MissionEvents.advanceToNext(data);
                    }
                    else
                    {
                        if (GameSettings.consoleLogging)
                        {
                            console.log("mission goal '" + type + "'' completed " + data._completed + "/" + data._required + " " + data._description);
                        }
//                        ga('send', 'event', GameSettings.VersionNumber, 'mission_complete', 'Trizzle-Arena4', type);
                        _emitter.emit("mission_goal", data);
                        // reset the goal after we complete each one
                        data._goalProgress -= data._goalNum;
                    }
                }
            }
        }

        // check if an active mission 'type' exists and reset the progress towards the next goal
        function missionReset(type: string)
        {
            // are there 'type' missions active
            let active:Array<MissionData> = MissionEvents.activeMissionType(type);
            for(let i:number = 0, l:number = active.length; i < l; i++)
            {
                // reset our progress towards the next goal
                let data:MissionData = active[i];
                data._goalProgress = 0;
            }
        }


        // event handlers

        // store a reference to this emitter so we can remove listeners in destroy()
        MissionEvents._emitter = _emitter;

        // reset all single game specific progress counters at the start of a new game
        _emitter.on("mission_cheat", function()
            {
                missionCheatTest(function(data: MissionData):boolean { return data._goalProgress >= data._goalNum; });
            });

        _emitter.on("all_missions_cheat", function()
            {
                allMissionsCheatTest(function(data: MissionData):boolean { return data._goalProgress >= data._goalNum; });
            });
        
        _emitter.on("game_start", function()
            {
                missionReset("move");
            });

        _emitter.on("score_changed", function(score, change)
            {
                missionCompleted("score", change, function(data: MissionData):boolean { return data._goalProgress - change < data._goalNum && data._goalProgress >= data._goalNum; });
            });

        _emitter.on("game_complete", function()
            {
                missionCompleted("completeGameAll", 1, function(data: MissionData):boolean { return data._goalProgress >= data._goalNum; });
            });

        _emitter.on("moves_changed", function(moves, change)
            {
                // the player has only completed a move when the moves counter decreases (-1)
                if (change < 0)
                {
                    // make the counter positive so the stock missionCompleted function will work correctly with it
                    change = -change;
                    // 'move' counts moves in a single game (progress is reset by game_start event calling missionReset above)
                    missionCompleted("move", change, function(data: MissionData):boolean { return data._goalProgress - change < data._goalNum && data._goalProgress >= data._goalNum; });
                    // 'moveAll' counts total moves across multiple games
                    missionCompleted("moveAll", change, function(data: MissionData):boolean { return data._goalProgress >= data._goalNum; });
                }
            });

        _emitter.on("level_changed", function(level, moves)
            {
                missionCompleted("levelUp", 0, function(data: MissionData):boolean { return level > 1 && moves == 1; });
                missionCompleted("reachLevel", 1, function(data: MissionData):boolean { return level == data._goalNum; } );
            });

        _emitter.on("matched_group", function(size, colour)
            {
                missionCompleted("match", size, function(data: MissionData):boolean { return size >= data._goalNum; });
                missionCompleted("matchAll", size, function(data: MissionData):boolean { return data._goalProgress >= data._goalNum; });
                if (colour == PieceColours.Blue)
                    missionCompleted("matchBlue", size, function(data: MissionData):boolean { return data._goalProgress >= data._goalNum; });
                if (colour == PieceColours.Green)
                    missionCompleted("matchGreen", size, function(data: MissionData):boolean { return data._goalProgress >= data._goalNum; });
                if (colour == PieceColours.Yellow)
                    missionCompleted("matchYellow", size, function(data: MissionData):boolean { return data._goalProgress >= data._goalNum; });
            });

        _emitter.on("megamatch_changed", function(megamatches, change)
            {
                missionCompleted("megaMatch", change, function(data: MissionData):boolean { return data._goalProgress - change < data._goalNum && data._goalProgress >= data._goalNum; });
            });

        _emitter.on("match_chain", function(chainLength, moves)
            {
                missionCompleted("chain", 1, function(data: MissionData):boolean { return chainLength >= data._goalNum; });
                if (moves >= GameSettings.InitialMoves)
                    missionCompleted("chain2", 1, function(data: MissionData):boolean { return chainLength == data._goalNum; });
            });
    }


    private static findWithId(id: number):MissionData
    {
        for(let i:number = MissionDescriptions.data.length - 1; i >= 0; --i)
        {
            if (MissionDescriptions.data[i]._id == id)
                return MissionDescriptions.data[i];
        }
        return null;
    }


    private static advanceToNext(current: MissionData):void
    {
        // find the 'current' mission in the activeMissions list
        let i:number = MissionEvents.activeMissions.indexOf(current);
        if (i != -1)
        {
            // find the 'current' mission in the MissionDescriptions list (which is indexed by openedTrophies)
            let d:number = MissionDescriptions.data.indexOf(current);
            if (d == -1)
            {
                if (GameSettings.consoleLogging)
                {
                    console.log("can't find this mission in the MissionDescriptions!");
                }
                return;
            }
            // find the 'current' mission in the openedTrophies list by looking for the value 'd'
            let t:number = GameSettings.openedTrophies.indexOf(d);
            if (t == -1)
            {
                if (GameSettings.consoleLogging)
                {
                    console.log("can't find a mission index " + d + " in openedTrophies!");
                }
                return;
            }

            // advance to the next mission index for the MissionDescriptions list
            let next:number = GameSettings.openedTrophies[t] + 1;

            // remove the completed mission from the list of open trophies
            GameSettings.openedTrophies.splice(t, 1);

            // missions are in groups of 5 with each group starting on a multiple of 5
            // this test makes sure that we don't advance out of our group and into the next group
            if (Math.floor(next / 5) == Math.floor(d / 5))
            {
                // replace the activeMissions list element with the next mission, or null if none is found
                MissionEvents.activeMissions[i] = MissionDescriptions.data[next];
                // insert the new active mission index into the list of open trophies (at the same location as the prior one)
                GameSettings.openedTrophies.splice(t, 0, next);

                if (GameSettings.consoleLogging)
                {
                    console.log("mission '" + current._name + "' replaced with mission '" + MissionEvents.activeMissions[i]._name + "'");
                }
            }
            else
            {
                // we finished this group, set null into the activeMissions (and don't add anything back into opened trophies)
                MissionEvents.activeMissions[i] = null;

                if (GameSettings.consoleLogging)
                {
                    console.log("mission '" + current._name + "' was the last one in its group");
                }

                let f : boolean = false;
                for(var m : number = 0; m < MissionEvents.activeMissions.length; m++)
                    if (MissionEvents.activeMissions[m] != null)
                        f = true;
                
                // are there ANY missions left at all?
                if (!f)
                    Analytics.logEvent("Badges", "All_Badges_Completed", "", "");
            }
        }
        else
        {
            if (GameSettings.consoleLogging)
            {
                console.log("mission '" + current._name + "' was not found in the activeMissions list!");
            }
        }
    }
    

    // find all active missions that match a given type
    private static activeMissionType(type: string):Array<MissionData>
    {
        let active:Array<MissionData> = new Array<MissionData>();
        for(let i:number = 0, l:number = MissionEvents.activeMissions.length; i < l; i++)
            if (MissionEvents.activeMissions[i])    // list entries become null when we run out of new missions
                if (MissionEvents.activeMissions[i]._type == type)
                    active.push(MissionEvents.activeMissions[i]);
        return active;
    }

}



