    public addBlanker():void
    {
        // the blanker for boost highlights, goes behind the play area and in front of the _ui
        this._blanker = new PIXI.Graphics();
        this._blanker.beginFill(0x000000, 0.5);
        this._blanker.drawRect(0, 0, 32, 32);
        this._blanker.endFill();
        this._blanker.visible = false;
        this._blanker.interactive = true;
        this._blanker.buttonMode = false;
        this._blanker.on("mouseup", this.onBlankerClicked.bind(this));
        this._blanker.on("touchend", this.onBlankerClicked.bind(this));
    }


