    private onBoostPaintButtonClicked():void
    {
        if (PlayArea.isBoostAllowed())
        {
            if (Inventory.powerup_paint > 0)
            {
                this.highlightBoostButton(0);
                this._blanker.visible = true;
                this._boostMode = BoostModes.BoostPaint;
                this._emitter.emit("boost_select", this._boostMode);
                this._emitter.emit("drag_enable", false);
            }
            else
            {
                let that: GameUi = this;
                // display shop to buy more boosts
                APIWrapper.purchaseBoosts(function(response: any) {
                    // refresh display values for all boost types
                    that._emitter.emit("show_boost_paint", Inventory.powerup_paint);
                    that._emitter.emit("show_boost_grow", Inventory.powerup_size_up);
                    that._emitter.emit("show_boost_destroy", Inventory.powerup_remove_doll);
                });
            }
        }
    }


